/*Typographys*/
.Modal {
  width: 30vw;
  min-height: 60vh;
  max-height: 80vh;
  position: fixed;
  top: 20vh;
  right: 0vw;
  background-color: white;
  display: grid;
  text-align: center;
  -webkit-justify-content: start;
          justify-content: start;
  vertical-align: top;
  padding: 10px 30px;
  box-shadow: 0px 3px 7px 10px gray;
  border-radius: 0px 0px 10px 10px;
  background-image: linear-gradient(to bottom right, purple, red);
  color: white;
