  border-radius: 5px;
  padding: 5px;
  -webkit-align-self: end;
          align-self: end; }

@-webkit-keyframes enter-modal {
  from {
    -webkit-transform: translate(160vw, 0px);
            transform: translate(160vw, 0px); }
  to {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px); } }

@keyframes enter-modal {
  from {
    -webkit-transform: translate(160vw, 0px);
            transform: translate(160vw, 0px); }
  to {
    -webkit-transform: translate(0px, 0px);
            transform: translate(0px, 0px); } }

